
.master{
    margin-top: 10rem;
    height: auto;
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $color-grey-light-1;

    @include respond (phone){
        flex-direction: column-reverse;
        margin-top: 3rem;
        
    }

}

.story {
    &__pictures {
        width: 50%;
        background-color: $color-primary;
        grid-column: full-start / col-end 4;
        background-image: linear-gradient(rgba($color-primary, .5), rgba($color-primary, .5)), url(../img/back.jpg);
        background-size: cover;

        display: grid;
        grid-template-rows: repeat(6, 1fr);
        grid-template-columns: repeat(6, 1fr);
        align-items: center;

        @include respond (phone){
            width: 100%;
        }

     
    };

    &__img--1 {
        width: 100%;
        grid-row: 2 / 6;
        grid-column: 2 / 6;
        box-shadow: 0 2rem 5rem rgba(#000, .1);

      
    };

    &__img--2 {
        width: 115%;
        grid-row: 4 / 6;
        grid-column: 4 / 7;
        z-index: 20;
        box-shadow: 0 2rem 5rem rgba(#000, .2);

        @include respond(phone){
            transform: translateX(-5rem)
        }

  
    };

    &__content{
        width: 50%;
        height: auto;
        grid-column: col-start 5 / full-end;
        padding: 6rem 8vw;
        
        
        display: grid;
        align-content: center;
        justify-items: start;
        gap: 2rem;

        @include respond (phone){
            width: 100%;
        }

        &__header{
            font-size: 1.6rem;
            color: #c69963;
            text-transform: uppercase;
        };

        &__header2{
            font-size: 4rem;
            font-style: italic;
            line-height: 1;
        };

        &__text{
            font-size: 1.5rem;
            font-style: italic;
            margin-bottom: 4rem;
        };

        &__button{
            background-color: #c69963;
            color: #fff;
            border: none;
            border-radius: 0;
            font-family: "Josefin Sans", sans-serif;
            font-size: 1.5rem;
            text-transform: uppercase;
            padding: 1.8rem 3rem;
            cursor: pointer;
            transition: all .2s;

            &:hover{
                background-color:$color-primary-dark;
            }

        };

        


      
    };

    &__text {
        font-size: 1.5rem;
        font-style: italic;
        margin-bottom: 4rem;
    };
};