.products{
    max-width: 120rem;
    height: auto;

    margin-left: auto;
    margin-right: auto;
    margin-top: 15rem;

    display: grid;
    

    grid-template-columns: repeat(auto-fit, minmax(33rem, 1fr));
    justify-content: center;
    align-items: center;
    gap: 3rem;
    row-gap: 8rem;
 


    &__card{
        
        position: relative;
        height: 46rem;
        background-color: #f9f7f6;
        width: 33rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-self: center;
        justify-self: center;

        &__img{
            width: 100%;
            flex-grow: 0;

            &__home-img{

                object-fit: fill;
                width: 100%;
                background-position: center;
                
            }



        }
        &__title-container{
            position: absolute;
            top:43%;
            width: 75%;
            height: 5rem;
            background-color: #101d2c;
            flex-grow: 2;
            display: flex;
            justify-content: center;
            align-items: center;
            &__text{
                font-size: 1.5rem;
                color: #fff;
            }



        }
        &__details-container{
            width: 100%;
            
            flex-grow: 2;
            display: grid;
            grid-template-columns: 1fr 1fr ;
            grid-template-rows: 1fr 1fr;
            justify-content: center;
           
            
            margin-bottom: 3rem;

            &__item{
                
                align-self:last baseline;
                justify-self: start;
                margin-left: 3rem; 
               

                &__item-text{
                    font-family: "Nunito", sans-serif;
                    font-weight: 300;
                    display: inline-block;
                    font-size: 1.5rem;
                    transform: translateY(-0.4rem);
                    margin-left: 1rem;
                }
                &__icon{
                    display: inline-block;
                    height: 2rem;
                    width: 2rem;
                    fill: #c69963;
                }
            }


        }
        &__action-button{
            width: 100%;
            flex-grow: 0.7;
            font-size: 1.4rem;
            background-color: #c69963;
            border: none;
            font-family: "Josefin Sans", sans-serif;
            color: #fff;
            transition: all 0.3s;

            &:hover{
                background-color: $color-primary-dark;
                cursor: pointer;
                transition: all 0.3s;
            }

        }
        
        &__icon{
            position: absolute;
            fill: #c69963;

            top: 1rem;
            height: 3rem;
            width: 3rem;
            right: 1rem;
        }

    }
    


}