.features{
    height: auto;//change to auto once responsive grid//
    
    max-width: 120rem;
    margin-left: auto;
    margin-right: auto;
    margin-top: 15rem; 

    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(40rem, 40rem));
    align-items: center;
    justify-content: center;

    @include respond(phone){
        margin-top: 50rem;

    }

    &__article{
        width: 40rem;
        height: 20rem;
        display: flex;
        gap: 2rem;

        @include respond(phone){
            width: 30rem;
            transform: translateX(4rem);
    
        }
     

        &__logo-container{
            

            &__icon{
                height: 5rem;
                width: 5rem;
                fill: #c69963;
                transform: translateY(-1.8rem);

            }
        }
        &__text-container{
            width: 30rem;

            &__header{
                font-size: 2rem;
                color: #54483A;
            }
            &__text{
                font-size: 1.5rem;
                margin-top: 2rem;
            }
        }

        
    }
   

    



}