.collage{
    margin-top: 15rem;
    
    height: auto;
    width: 100%;

    background-color: $color-grey-light-1;

    display:grid;
    grid-template: repeat(7, 5vw) / repeat( 8, 1fr);

    grid-gap: 1.5rem;
    padding: 1.5rem;
   
}

.gallery__item{
    &--1 {
        grid-row: 1 / span 2;
        grid-column: 1 / span 2;
    }

    &--2 {
        grid-row: 1 / span 3;
        grid-column: 3 / span 3;
    }

    &--3 {
        grid-row: 1 / span 2;
        grid-column: 6 / 7;
    }

    &--4 {
        grid-row: 1 / span 2;
        grid-column: 7 / -1;
    }

    &--5 {
        grid-row: 3 / span 3;
        grid-column: 1 / span 2;
    }

    &--6 {
        grid-row: 4 / span 2;
        grid-column: 3 / span 2;
    }

    &--7 {
        grid-row: 4 / 5;
        grid-column: 5 / 6;
    }

    &--8 {
        grid-row: 3 / span 2;
        grid-column: 6 / span 2;
    }

    &--9 {
        grid-row: 3 / span 3;
        grid-column: 8 / -1;
    }

    &--10 {
        grid-row: 6 / span 2;
        grid-column: 1 / 2;
    }

    &--11 {
        grid-row: 6 / span 2;
        grid-column: 2 / span 2;
    }

    &--12 {
        grid-row: 6 / span 2;
        grid-column: 4 / 5;
    }

    &--13 {
        grid-row: 5 / span 3;
        grid-column: 5 / span 3;
    }

    &--14 {
        grid-row: 6 / span 2;
        grid-column: 8 / -1;
    }
    
}

.gallery__img{
    width: 100%;
        height: 100%;
        object-fit: cover;
        display: block;

}