.header{
    background-color: blue;
    height: 90vh;
   
    width: 100%;
    position: relative;
    padding-left: $padding-left-compensation-navigation;//compensate for navigation bar

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    

    @include respond(phone){

        padding-left: 0rem;
        display: inline-block;
        flex-direction: column;
        width: 100%;
        height: 100dvh;
        padding-top: 6rem;
    }

    &__left-side{
        height: 100%;
        width: 65%;
        background-image: linear-gradient(rgba(16, 29, 44, 0.93), rgba(16, 29, 44, 0.93)), url(../img/hero.jpeg);
        background-position: center;
        background-size: cover;
        position: relative;

        display: flex;

        flex-direction: column;

        align-items: center;

        @include respond(phone){
            width: 100%;
        }

        &__img{
            height: 3.5rem;
           
            margin-top: 5rem;
            align-self: center;
            justify-self: center;
            position: absolute;

        }

        &__sub-section{
            
            width: 75%;

            display: flex;
            flex-direction: column;
            justify-content:center;
            align-items: start;
            height: 40%;
            margin-top: auto;
            margin-bottom: auto;
            

            &__header{
                font-size: 2rem;
                font-family: "Josefin Sans", sans-serif;
                color: #c69963;
                @include respond(tab-port){
                    font-size: 2rem;
                }

               
            }

            &__sub-header{
                color: white;
                font-size: 5rem;
                font-family: "Josefin Sans", sans-serif;
                font-weight: 400;
                line-height: 7rem;
                @include respond(tab-port){
                    font-size: 4rem;

                    line-height: 5rem;
                }

            }
            &__button{
                background-color:$color-primary;
                border: none;
                font-size: 1.7rem;
                height:5rem;
                width: 28rem;
                color: white;
                font-family: "Josefin Sans", sans-serif;
                font-weight: 400;
                text-transform: uppercase;
                transition: all 0.3s;
                &:hover{
                    background-color: $color-primary-dark;
                    cursor: pointer;
                    transition: all 0.3s;
                  
                }

                @include respond(phone){
                    width: 22rem;
                }
            }


        }

        &__sub-section-2{
            position: absolute;

            bottom: 0rem;
            width: 75%;
            height: 15rem;
            
            display: flex;
            align-items: center;
            justify-content: space-around;

            @include respond(phone){
                height: 10rem;
                gap: 1rem;
            }

            &__img{
                height: 2rem;
                transform: translateY(-2rem);
                @include respond(tab-port){
                    height: 1.5rem;
                    transform: translateY(0rem);
                }
            

            
                
            }

            &__text{
                position: absolute;
                top:-1.5rem;
                font-size: 2rem;
                color: $color-grey-light-2;
            }

            &__div-trick{
                
                height: 100%;
                width: 100%;
                position: absolute;
                display: flex;

                justify-content: space-between;
                
              

                &-1{
                    border-top: solid $color-grey-light-2 1px;
                  
                    height: 100%;
                    width: 42%;
                    display: inline-block;

                    @include respond(tab-land){
                        width: 39%;
                    }
                    @include respond(tab-port){
                        width: 36%;
                    }

                  
                    
                

                }
                &-2{
                    border-top: solid $color-grey-light-2 1px;
                  
                    height: 100%;
                    width: 42%;
                    display: inline-block;
                    @include respond(tab-land){
                        width: 39%;
                    }
                    @include respond(tab-port){
                        width: 36%;
                    }
                    
                }
            }
        }
        

    }

    &__right-side{
        height: 100%;
        flex-grow: 1;
        background-color:#101d2c;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 2rem;

        @include respond(phone){
        
            width: 100%;
            flex-direction: column;
            height: 50rem;
        }

        &__header{
            font-size: 1.6rem;
            color: #c69963;
            
        }

        &__realtor{
            height: 10rem;
            
            width: 30rem;

            display: flex;
            flex-direction: row;
            gap: 2rem;
            justify-content: center;
            align-items: center;

            &__img{
                height: 8rem;
                border-radius: 20rem;
                
            }
            &__text-container{
              &__text-1{
                color: white;
                font-size: 2rem;
                font-family: "Josefin Sans", sans-serif;
                font-weight: 400;
              }
              &__text-2{
                color: $color-grey-light-2;
                font-family: "Josefin Sans", sans-serif;
                
              }
            }

            
        }

    

    }
}
