.footer{
    padding-top: 5rem;
    background-color: #101d2c;
    height: auto;
    width: 100%;
    padding-left: $padding-left-compensation-navigation;//compensate for navigation bar

    @include respond(phone){
        padding-left: 0rem;//stop compensating for navigation bar
    }

    &__upper{
        width: 100%;
        margin-left: auto;
        margin-right: auto;

        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(23rem, 23rem));
        align-items: center;
        justify-content: center;
        
        gap: 0rem;

        &__link-container{
           
            width: 23rem;
            margin-left: auto;
            margin-right: auto;
            height: 10rem;
          
            
            align-self: center;
            justify-content: center;
            display: flex;
            justify-content: center;
            align-items: center;
            

            &__link{
                
                color: white;
                font-size: 1.3rem;
                display: inline-block;
                

                padding: 1rem 2rem 1rem 2rem;
                transition: all 0.3s;

                list-style: none;
                text-decoration: none;

                &:hover{
                    cursor: pointer;
                    transform: translateY(-1rem);

                    transition: all 0.3s;
                    background-color: #1d344e;

                }
            
            }
        }

    }

    &__lower{
        height: 15rem;
        width: 70%;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        align-items: center;
        justify-content: center;

        &__credits{
            font-size: 1.4rem;
            color: #aaa;
            transform: translateY(-1.4rem);
            
        }
    }




 





}
