.navigation{
    background-color: $color-primary;
    width: 8rem;
    height: 100%;
    margin-left: 0;
    
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;

    overflow: hidden;

    display: flex;
    
    justify-content: center;

    @include respond(phone){
        position: absolute;
        width: 100%;
        height: 7rem;
    }

    &__button{

        width: 5rem;
        height: 5rem;
        margin-top: 2rem;
       justify-self: center;
       background-color: transparent;

       display: flex;
       align-items: center;
     


       border: none;

       

       &:hover{
        cursor: pointer;
       }
       
       @include respond(phone){
        position: absolute;
        right: 2rem;
        top:-1rem;
    
        
    }

       

       &__nav-icon{
            font-size: 10rem;
            color: white;
            transform: scale(1.3);
            font-weight: 400;
            
            height: 5rem;
            
            
       };

    };

}

